/* Contact window Win95/retro styles */

.contact-content {
  padding: 1.5rem 1.5rem 1rem 1.5rem;
  background: #c0c0c0;
  color: #222;
  font-size: 15px;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1rem;
}

.contact-form label {
  font-family: 'MS Sans Serif', 'Tahoma', 'Geneva', sans-serif;
  font-size: 15px;
  color: #000080;
}

.contact-form input,
.contact-form textarea {
  font-family: inherit;
  font-size: 15px;
  border: 2px solid #808080;
  background: #fff;
  color: #222;
  padding: 0.5rem;
  border-radius: 0;
  box-shadow: 1px 1px 0 #fff inset;
}

.contact-form button {
  margin-top: 0.5rem;
}

@media (max-width: 700px) {
  .contact-content {
    padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    font-size: 14px;
  }
  .contact-form label {
    font-size: 13px;
  }
  .contact-form input, .contact-form textarea {
    font-size: 13px;
    padding: 0.25rem;
  }
}
@media (max-width: 480px) {
  .contact-content {
    padding: 0.25rem 0.25rem 0.25rem 0.25rem;
    font-size: 12px;
  }
  .contact-form label {
    font-size: 11px;
  }
  .contact-form input, .contact-form textarea {
    font-size: 11px;
    padding: 0.15rem;
  }
}
